<template>
  <div class="container" :class="classBaseOnSend">
    <h5 v-if="!send">{{ who }}</h5>
    <div class="message">
      <p>
        <slot></slot>
      </p>
      <div class="time">
        {{ when }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["who", "date"],
  computed: {
    send() {
      if (this.who === this.$store.getters["userEmail"]) {
        return true;
      } else {
        return false;
      }
    },
    classBaseOnSend() {
      return this.send ? "ours" : "others";
    },
    when() {
      let d = new Date(this.date)
      let hour = d.getHours()
      let minutes = d.getMinutes()

      return hour + ':' + minutes
    }
  },
};
</script>

<style scoped>
.container {
  display: flex;
  /* justify-content: center; */
  align-items: center;
  gap: 10px;
  margin: 15px 20px;
}
.message {
  max-width: 400px;
  min-width: 35px;
  border-radius: 12px;
  background-color: #6a44ff;
  padding: 1px 5px;
  font-size: 12px;
  letter-spacing: 1px;
  color: white;
  position: relative;
}
h5 {
  color: #6a44ff;
}
.ours {
  justify-content: flex-end;
}
.ours .message {
  font-weight: 500;
}
.others .message {
  background-color: #29292c;
}
.time {
  position: absolute;
  bottom: 4px;
  right: 4px;
  font-size: 10px;
  color: lightgray;
  margin-top: 10px;
}
.message p {
  margin-bottom: 22px;
}
/* @media screen and (max-width: 480px) {
  .container {
    display: flex;
    flex-direction: column;
  }
} */
</style>
