let timer;

export default {
  state() {
    return {
      userId: null,
      token: null,
      tokenExpiration: null,
      userEmail: null,
    };
  },
  mutations: {
    setUser(state, payload) {
      state.token = payload.token;
      state.userId = payload.userId;
      state.userEmail = payload.userEmail;
    },
  },
  actions: {
    async logIn(context, payload) {
      const responce = await fetch(
        "https://identitytoolkit.googleapis.com/v1/accounts:signInWithPassword?key=AIzaSyBK-vZsM_-3gF1m2YOmit8CM52UN-5VO7A",
        {
          method: "POST",
          body: JSON.stringify({
            email: payload.email,
            password: payload.password,
            returnSecureToken: true,
          }),
        }
      );

      const responceData = await responce.json();

      if (!responce.ok) {
        console.log(responceData);
        const error = new Error(
          responceData.message || "Failed to authenticate"
        );
        throw error;
      }

      const expiresIn = +responceData.expiresIn * 1000;
      // const expiresIn = 5000;
      const expirationDate = new Date().getTime() + expiresIn;

      localStorage.setItem("token", responceData.idToken);
      localStorage.setItem("userId", responceData.localId);
      localStorage.setItem("userEmail", responceData.email);
      localStorage.setItem("tokenExpiration", expirationDate);

      timer = setTimeout(() => {
        context.dispatch("logout");
      }, expiresIn);

      console.log(responceData);
      context.commit("setUser", {
        token: responceData.idToken,
        userId: responceData.localId,
        userEmail: responceData.email,
      });
    },
    async signUp(context, payload) {
      const responce = await fetch(
        "https://identitytoolkit.googleapis.com/v1/accounts:signUp?key=AIzaSyBK-vZsM_-3gF1m2YOmit8CM52UN-5VO7A",
        {
          method: "POST",
          body: JSON.stringify({
            email: payload.email,
            password: payload.password,
            returnSecureToken: true,
          }),
        }
      );

      const responceData = await responce.json();

      if (!responce.ok) {
        console.log(responceData);
        const error = new Error(
          responceData.message || "Failed to authenticate"
        );
        throw error;
      }

      const expiresIn = +responceData.expiresIn * 1000;
      // const expiresIn = 5000;
      const expirationDate = new Date().getTime() + expiresIn;

      localStorage.setItem("token", responceData.idToken);
      localStorage.setItem("userId", responceData.localId);
      localStorage.setItem("userEmail", responceData.email);
      localStorage.setItem("tokenExpiration", expirationDate);

      timer = setTimeout(() => {
        context.dispatch("logout");
      }, expiresIn);

      console.log(responceData);
      context.commit("setUser", {
        token: responceData.idToken,
        userId: responceData.localId,
        userEmail: responceData.email,
      });
    },
    tryLogin(context) {
      const token = localStorage.getItem("token");
      const userId = localStorage.getItem("userId");
      const userEmail = localStorage.getItem("userEmail");
      const tokenExpiration = localStorage.getItem("tokenExpiration");

      const expiresIn = +tokenExpiration - new Date().getTime();

      if (expiresIn < 10000) {
        return;
      }

      timer = setTimeout(() => {
        context.dispatch("logout");
      }, expiresIn);

      if (token && userId && userEmail) {
        context.commit("setUser", {
          token: token,
          userId: userId,
          userEmail: userEmail,
        });
      }
    },
    logout(context) {
      localStorage.removeItem("token");
      localStorage.removeItem("userId");
      localStorage.removeItem("userEmail");
      localStorage.removeItem("tokenExpiration");

      clearTimeout(timer);

      context.commit("setUser", {
        token: null,
        userId: null,
        userEmail: null,
      });
    },
  },
  getters: {
    userEmail(state) {
      return state.userEmail;
    },
    token(state) {
      return state.token;
    },
  },
};
