import { createStore } from "vuex";
import authModule from "./auth.js";
export default createStore({
  modules: {
    auth: authModule,
  },
  state() {
    return {
      allMessages: [],
    };
  },
  getters: {
    allMessages(state) {
      return state.allMessages;
    },
  },
  mutations: {
    addMessage(state, payload) {
      state.allMessages.push(payload);
    },
    loadMessages(state, payload) {
      state.allMessages = payload;
    },
  },
  actions: {
    async addMessage(context, payload) {
      const messageData = {
        message: payload.message,
        userEmail: payload.userEmail,
        date: payload.date,
      };

      const token = context.rootGetters.token;

      const responce = await fetch(
        "https://message-me-4d640-default-rtdb.firebaseio.com/messages.json?auth=" +
          token,
        {
          method: "POST",
          body: JSON.stringify(messageData),
        }
      );
      const responceData = await responce.json();
      if (!responce.ok) {
        const error = new Error(responceData.message || "Cant send message");
        throw error;
      }

      context.commit("addMessage", messageData);
    },
    async loadMessages(context) {
      const responce = await fetch(
        "https://message-me-4d640-default-rtdb.firebaseio.com/messages.json"
      );
      const responceData = await responce.json();
      const messages = [];
      for (const mes in responceData) {
        const message = {
          message: responceData[mes].message,
          userEmail: responceData[mes].userEmail,
          date: responceData[mes].date,
        };
        messages.push(message);
      }
      context.commit("loadMessages", messages);
    },
    async loadNewMessages(context) {
      const responce = await fetch(
        "https://message-me-4d640-default-rtdb.firebaseio.com/messages.json"
      );
      const responceData = await responce.json();
      const messages = [];
      for (const mes in responceData) {
        const message = {
          message: responceData[mes].message,
          userEmail: responceData[mes].userEmail,
          date: responceData[mes].date,
        };
        messages.push(message);
      }
      const allMessages = context.state.allMessages;
      // console.log(Array.isArray(allMessages));
      // console.log(Array.isArray(messages));

      if (messages.length > allMessages.length) {
        const newMessageCount = messages.length - allMessages.length;
        const newMessages = messages.slice(-newMessageCount);
        // for (const mes in newMessages) {
        //   context.state.allMessages.push(newMessages[mes])
        //   console.log(newMessages[mes]);
        //   window.scrollTo({
        //     top: document.body.scrollHeight,
        //     behavior: "smooth",
        //   });
        // }
        context.state.allMessages.push(...newMessages);
        setTimeout(() => {
          window.scrollTo({
            top: document.body.scrollHeight,
            behavior: "smooth",
          });
        }, 0);
      } else {
        console.log("no new messages");
      }
    },
  },
});
