<template>
  <div class="backdrop">
    <error-message @closeError="handleError" v-if="!!error">
      <p>{{ error }}</p>
    </error-message>
    <div class="loginForm">
      <form @submit.prevent="submitForm">
        <h2>Sign up</h2>
        <div class="input-container">
          <label for="email">Your email</label>
          <input type="email" id="email" v-model.trim="email" />
        </div>
        <div class="input-container">
          <label for="password">Your password</label>
          <input type="password" id="password" v-model.trim="password" />
        </div>
        <div class="button-container">
          <button class="actionButton" :disabled="!formIsValid">
            {{ actionButtonDesc }}
          </button>
        </div>
        <span class="loader" v-if="isLoading"></span>
        <div class="button-container">
          <button type="button" class="changeModeButton" @click="changeMode">
            {{ changeModeButtonDesc }}
          </button>
          insted.
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import ErrorMessage from "./ErrorMessage.vue";
export default {
  components: {
    ErrorMessage,
  },
  data() {
    return {
      email: "",
      password: null,
      mode: "signup",
      isLoading: false,
      error: null,
      formIsValid: true,
    };
  },
  methods: {
    async submitForm() {
      if (!this.email.includes("@") || this.password < 6) {
        return;
      }

      this.isLoading = true;

      try {
        if (this.mode === "signup") {
          await this.$store.dispatch("signUp", {
            email: this.email,
            password: this.password,
          });
        } else {
          await this.$store.dispatch("logIn", {
            email: this.email,
            password: this.password,
          });
        }
      } catch (error) {
        this.error = error.message || "fialed to authenticate.";
      }

      this.isLoading = false;
      console.log(this.$store.getters["userEmail"]);
    },

    changeMode() {
      if (this.mode === "signup") {
        this.mode = "login";
      } else {
        this.mode = "signup";
      }
    },
    handleError() {
      this.error = null;
    },
  },
  computed: {
    actionButtonDesc() {
      if (this.mode === "signup") {
        return "Sign up";
      } else {
        return "Login";
      }
    },
    changeModeButtonDesc() {
      if (this.mode === "signup") {
        return "Login";
      } else {
        return "Sign up";
      }
    },
  },
};
</script>

<style scoped>
.backdrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(255, 255, 255, 0.4);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  z-index: 200;
}
.loginForm {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid white;
  padding: 15px 30px;
  border-radius: 7px;
}
h2 {
  text-align: center;
  color: white;
}

.input-container {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  gap: 5px;
}
input {
  width: 300px;
  height: 30px;
  border-radius: 7px;
  border: none;
}
input:focus {
  border: none;
  outline: none;
}
label {
  color: lightgray;
  font-size: 12px;
}
.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
}
.actionButton {
  width: 300px;
  height: 30px;
  border-radius: 100px;
  font: inherit;
  font-weight: 500;
  background-color: #6a44ff;
  border-bottom: 2px solid white;
  border-right: 2px solid white;
  border-top: 2px solid black;
  border-left: 2px solid black;
}
.changeModeButton {
  width: 100px;
  height: 30px;
  border-radius: 100px;
  font: inherit;
  font-weight: 500;
  background-color: #6a44ff;
  border-bottom: 2px solid white;
  border-right: 2px solid white;
  border-top: 2px solid black;
  border-left: 2px solid black;
  margin-right: 10px;
}
.loader {
  width: 40px;
  height: 40px;
  border: 3px solid #fff;
  border-bottom-color: transparent;
  border-radius: 50%;
  display: inline-block;
  margin-left: 130px;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>
