<template>
  <log-in v-if="!userEmail"></log-in>
  <error-message v-if="!!error">
    {{ error }}
  </error-message>
  <the-header :userEmail="userEmail"></the-header>
  <div class="chatPage-container">
    <div class="messages">
      <p v-if="messages.length <= 0">no messages yet, write one.</p>
      <transition-group name="slideIn">
        <message-bar
          v-for="mes in messages"
          :key="mes"
          :who="mes.userEmail"
          :date="mes.date"
        >
          {{ mes.message }}
        </message-bar>
      </transition-group>
    </div>
    <div class="text-input-container">
      <input
        type="text"
        placeholder="Type somthing..."
        v-model.trim="inputMessage"
        @keyup.enter="sendMessage"
      />
      <img src="../assets/send.png" alt="" @click="sendMessage" />
    </div>
  </div>
</template>

<script>
import messageBar from "@/components/messanger/messageBar.vue";
import LogIn from "@/components/messanger/LogIn.vue";
import ErrorMessage from "@/components/messanger/ErrorMessage.vue";
import TheHeader from "@/components/messanger/TheHeader.vue";
export default {
  beforeCreate() {
    document.body.className = "chatPage";
  },
  components: {
    messageBar,
    LogIn,
    ErrorMessage,
    TheHeader,
  },
  data() {
    return {
      inputMessage: "",
      error: null,
    };
  },
  methods: {
    async sendMessage() {
      if (this.inputMessage.length <= 0) {
        return;
      }
      const messageData = {
        message: this.inputMessage,
        userEmail: this.userEmail,
        date: new Date(),
      };
      try {
        await this.$store.dispatch("addMessage", messageData);
        this.inputMessage = "";
        window.scrollTo({
          top: document.body.scrollHeight,
          behavior: "smooth",
        });
      } catch (err) {
        this.error = err.message || "Cant send message";
      }
    },
  },
  computed: {
    messages() {
      return this.$store.getters["allMessages"];
    },
    dynamicClass() {
      return {
        active: this.inputMessage.length > 0,
        notActive: this.inputMessage.length <= 0,
      };
    },
    userEmail() {
      return this.$store.getters["userEmail"];
    },
  },
  async created() {
    await this.$store.dispatch("loadMessages");
    window.scrollTo({
      top: document.body.scrollHeight,
      behavior: "smooth",
    });
    setInterval(() => {
      this.$store.dispatch("loadNewMessages");
    }, 2000);
  },
};
</script>

<style scoped>
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap");
* {
  font-family: "Roboto Mono", monospace;
}
.chatPage-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.messages {
  flex: 1 1 auto;
  overflow-y: auto;
  padding-bottom: 75px;
  padding-top: 40px;
}
.text-input-container {
  /* flex: 0 1 10%; */
  background-color: #1e1e20;
  z-index: 100;
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  gap: 15px;
  /* height: 60px; */
}
.text-input-container input {
  background-color: #141414;
  flex: 1;
  border: none;
  margin-left: 100px;
  /* letter-spacing: 1px; */
  height: 50px;
  color: white;
  border-radius: 100px;
  padding: 0 10px;
}
.text-input-container input:focus {
  border: none;
  outline: 1px solid #6a44ff;
}

.text-input-container img {
  height: 30px;
  border: none;
  border-radius: 100%;
  padding: 10px;
  margin-right: 100px;
  object-fit: fill;
  background-color: #6a44ff;
  /* margin-left: 15px; */
  cursor: pointer;
}

@media screen and (max-width: 480px) {
  .text-input-container {
    justify-content: center;
    padding: 15px 0;
  }
  .text-input-container input {
    height: 40px;
  }
  .text-input-container img {
    height: 20px;
    padding: 7px;
  }
}

.slideIn-enter-active,
.slideIn-leave-active {
  transition: all 0.5s ease;
}

.slideIn-enter-from,
.slideIn-leave-to {
  opacity: 0;
  transform: translateX(-30px);
}
p {
  color: white;
  text-align: center;
  margin-top: 10px;
}
</style>
